.purchase__wrap {
  background-image: linear-gradient(180deg, @bg-light 8%, @bg-decor 8%);
}
.purchase__inset {
  .no-list-style();
  display: flex;
  justify-content: center;
}
.purchase__terms {
  border-top: 1px solid rgb(0, 0, 0);
}
.purchase__terms {
  position: relative;
  min-height: 250px
}
.purchase__terms-item {
  .no-list-style();
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  transition: opacity 0.2s linear;
  z-index: 0;
   &.active {
    opacity: 1;
    z-index: 1;

  }
}
@media(max-width:@butdesktop-width-only) {
  .purchase__inset-item .purchase__inset-link {
  display: block;
  padding: 7px;
  margin-right: 10px;
  color: @main-color-text;
  border: 2px solid @main-color-hover;
  background-color: @main-color;
  font-weight: 800;
  box-shadow: 0 0 10px 0 @shadow;
  font-size: 16px;
  letter-spacing: normal;
  &::before {
    display: none;
  }

  &:hover,
  &:focus,
  &.active-tab {
    color: @dark-color-text;
    background-color: @bg-light;
  }
}
}
@media(min-width:@tablet-width) {
  .purchase__terms-text {
    max-width: 85%;
  }
  .purchase__terms-item {
    width: 100%;
    min-height: 100%;
    background-repeat: no-repeat;
    background-position: right 30px bottom 40px;

