@media(min-width:@desktop-width) {
  .purchase {
    padding-bottom: 109px;
    background-image: linear-gradient(180deg, @bg-light 20%, @btn-disabled 20%);
  }
  .purchase__wrap {
    display: flex;
    justify-content: space-between;
    padding-left: 0;
    padding-right: 0;
    background-image: none;
  }
  .purchase__inset {
    position: relative;
    display: flex;
    flex-direction: column;
    min-width: 285px;
    padding-top: 50px;
    &::before {
      position: absolute;
      content: "";
      right: -2px;
      top: -24px;
      width: 7px;
      height: 320px;
      background-color: @dark-color-text;
    }
  }
  .purchase__terms {
    .no-list-style();
    width: 760px;
    border-top: none;
  }
  .purchase__terms-title {
    margin-bottom: 25px;
    font-family: "Gilroy", "Arial", sans-serif;
    font-size: 47px;
    line-height: 1.2;
    letter-spacing: 0.01em;
    color: @dark-color-text;
  }
  .purchase__terms-text {
    max-width: 425px;
  }
  .purchase__terms-item--delivery {
    background-position: right 60px top 60px;
    background-size: 136px 164px;
  }
  .purchase__terms-item--guarantee {
    background-size: 171px 195px;
    background-position: right 60px top 40px;
  }
  .purchase__terms-item--credit {
    background-size: 156px 188px;
    background-position: right 60px top 40px;
  }
  .purchase__inset-item {
    margin-bottom: 20px;
  }
  .purchase__inset-link {
    width: 160px;
    margin-right: 0;
    text-align: center;
    transition: all 0.2s linear;
    &.active-tab::before {
      background-color: @dark-color-link;
    }
    &.active-tab {
      width: 100%;
      padding-right: 120px;
      color: @main-color;
      background-color: @dark-color-link;
    }
  }
}
